.wrapper{height:fit-content;background:#000;background:linear-gradient(30deg, rgb(23, 23, 23) 0%, rgb(49, 49, 49) 40%, rgb(0, 0, 0) 65%);padding:5rem;margin-bottom:15rem}.container{display:flex;height:90vh;width:70%;margin:auto}.img{max-width:30vw}.сalculator{background-color:#fff;width:60%;height:fit-content;margin-top:.5rem;padding:2rem 1.1rem 0}.сalculator h2{color:#000;font-size:2rem;text-transform:uppercase;font-weight:800;margin-bottom:3rem}.desc{color:rgba(0,0,0,.7529411765);margin-bottom:1rem}.logic{display:flex;gap:2rem}.logic>div{width:50%}.buttons{display:flex;justify-content:space-between;margin-bottom:3rem}.buttons button{width:49%;transition:.1s all}.genderBtn{background-color:rgba(0,0,0,0);border:1px solid #000;padding:1rem 2rem}.active{background-color:#ff7f27;border:1px solid rgba(0,0,0,0);color:#fff;padding:1rem 2rem}.input{width:100%;border:1px solid rgba(0,0,0,.3215686275);margin-bottom:1rem;padding:1rem;box-sizing:border-box}.input::placeholder{transition:.2s color}.input:focus::placeholder{color:rgba(0,0,0,0)}.select{width:100%;border:1px solid rgba(255,255,255,.3215686275);margin-bottom:1rem;padding:1rem;box-sizing:border-box;outline:none;border-radius:0}.select option{border-radius:0}.calculate{width:100%;background-color:#000;color:#fff;text-transform:uppercase;font-size:1rem;font-weight:800;padding:1rem 2rem}.calculate:hover{color:#000;background-color:#fff;border:1px solid #000}.resultContainer{height:fit-content}.result{aspect-ratio:1/1;color:#fff;width:100%;background:#202020;background:linear-gradient(90deg, rgb(32, 32, 32) 0%, rgb(0, 0, 0) 100%);padding:2rem;margin-bottom:1rem}.result h4{font-size:2rem;margin-bottom:1rem}.result h4 span{font-size:1.5rem}.result .desc{color:rgba(255,255,255,.9058823529)}.details{color:rgba(0,0,0,.7176470588);height:50%;max-height:12rem;overflow-y:auto}@media(max-width: 1024px){.wrapper{height:45rem;padding:10rem 0;margin-bottom:10rem}.container{justify-content:space-around;width:90%}.result{aspect-ratio:auto}.logic{flex-direction:column}.logic>div{width:100%}.img{max-width:40vw;z-index:1}}@media(max-width: 768px){.wrapper{padding:5rem 0;height:fit-content}.container{height:fit-content;flex-direction:column}.container>div{width:100%}.img{max-width:100%}.details{font-size:1.2rem}}